.form-control {
  margin: 1rem 0;
}

.form-control label,
.form-control input {
  display: block;
}

.form-control label {
  font-weight: bold;
  margin-bottom: 0.5rem;
}

.form-control input {
  width: 100%;
  font: inherit;
  border: 2px solid #ccc;
  background: #f8f8f8;
  padding: 0.35rem 0.65rem;
}

.form-control input:focus {
  outline: none;
  background: #ebebeb;
  border-color: #510077;
}

.form-control--invalid label,
.form-control--invalid p {
  color: red;
}

.form-control--invalid input {
  border-color: red;
  background: #ffd1d1;
}
