.navbar {
  width: 100%;
  height: 75px;
  background-color: #131212;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem 1rem;
  z-index: 50;
}

.navbar__logo h2 {
  color: #f4f4f4;
  font-size: 1.8rem;
}

.navbar__links {
  display: flex;
  list-style: none;
  align-items: center;
}

.navbar__links > li {
  padding-left: 1.5rem;
}

.navbar__links > li > a {
  text-decoration: none;
  color: #f4f4f4;
  font-size: 1.2rem;
  display: flex;
  align-items: center;
}

.users__link {
  background: #333;
  padding: 10px;
  border-radius: 8px;
}

.users__link:hover {
  background: #2b6eda;
  color: #f4f4f4;
}

.active {
  background: #2b6eda;
  color: #f4f4f4;
}

.hamburger__menu {
  width: 30px;
  height: 30px;
  display: none;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
}

.hamburger__menu > div {
  width: 100%;
  height: 3px;
  background-color: #f4f4f4;
}

.hamburger__menu:hover > div {
  background-color: #dd219e;
}

@media (max-width: 960px) {
  .navbar__links {
    display: none;
  }

  .hamburger__menu {
    display: flex;
  }
}

@media (max-width: 500px) {
  .navbar__logo > h2 {
    font-size: 1.4rem;
  }
}
